import {preview,addressdata} from '../../common/list.js'
import {mapState} from 'vuex'
//引入消息提示插件
 import HMmessages from "@/components/HM-messages/HM-messages.vue"
export default {
	name: 'travels',
	components: {HMmessages},
	data() {
		return {
			num: 0,
			fication: [
				{
					"name":'心情'
				},
				{
					"name":'书籍资料'
				},
				{
					"name":'食品'
				},
				{
					"name":'生活'
				},
				{
					"name":'手机'
				},
				{
					"name":'电子'
				},
				{
					"name":'彩妆'
				},
				{
					"name":'男装'
				},
				{
					"name":'女装'
				},
				{
					"name":'其他'
				},
			],

			uploadvideos: false,
			watchaddress: '',
			classdata: '心情', //分类
			titledata: '', //标题
			tipsdata: '', //描述
			topimg: [], //上传的图片
			videos: '', //上传的视频
			address: '', //选择的城市
			avatarUrl: '', // 用户头像
			nickName: '', // 用户昵称
			openid: '', // 用户openid
			// 提示用户正在发布
			reldata: '正在发布...请勿关闭该页面',
			relend: false
		};
	},
	methods:{
		//点击分类切换
		menubtn(index,name){
			this.num = index
			this.classdata = name
		},
		//上传图片
		uploadImg(){
			uni.chooseImage({
			    count: 9, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: (res)=> {
			        console.log(...res.tempFilePaths);
					this.topimg.push(...res.tempFilePaths)
					
			    }
			});
		},
		//删除图片
		deleteImg(index){
			this.topimg.splice(index,1)
		},
		//预览图片
		preImage(index){
			// 预览图片
			let imglist = this.topimg
			preview(index,imglist)
			.then((res)=>{
				console.log('预览成功')
			})
			.catch((err)=>{
				console.log('预览失败')
			})
		},
		//上传视频
		uploadVideo(){
			uni.showLoading({
			    title: '上传中'
			});
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				maxDuration:20,
			})
			.then((res)=>{
				console.log(res)
				this.videos = res[1].tempFilePath
				this.uploadvideos = true
				uni.hideLoading();
			})
			.catch((err)=>{
				console.log(err)
			})
		},
		//删除视频
		deleteVideo(){
			this.videos = ''
			this.uploadvideos = false
		},
		//定位
		addRess(){
			addressdata()
			.then((res)=>{
				console.log(res)
				let city = res.result.address_reference.landmark_l2.title
				this.address = city
			})
			.catch((err)=>{
				console.log("用户拒绝定位")
				this.address = '新校区'
			})
		},
		//跳转到选择城市页面
		chooseCity(){
			uni.navigateTo({
				url:'../city/city'
			})
		},
		//点击获取数据上传
		suBmitd(){
			console.log(123456)
			console.log(this.classdata)
			console.log(this.titledata)
			console.log(this.tipsdata)
			console.log(this.topimg)
			console.log(this.videos)
			console.log(this.address)
			if(this.titledata == ''){
				this.proMpt('标题必填')
			}else if(this.tipsdata == ''){
				this.proMpt('描述必填')
			}else if(this.topimg.length<3){
				this.proMpt('上传图片不少于三张')
			}
		},
		
		//及时反馈
		proMpt(tip){
			this.HMmessages.show(tip,{
				icon:'error',
				iconColor:'#ffffff',
				fontColor:'#ffffff',
				background:'rgba(102,0,21,0.8)'
			})
		}
		
	},
	created() {
		//定位
		this.addRess()
	},
	//计算属性
	computed:{
		...mapState(['travecity']),
		count(){
			this.watchaddress = this.travecity.traveing
		}
	},
	//侦听器
	watch:{
		watchaddress(newValue,oldValue){
			console.log(newValue)
			this.address = newValue
		}
	}
};
